.projectImage {
  height: 200px;
  width: 300px;
}

.footer {
  position: relative;
  bottom: 0;
  width: 100%;
  padding: 20px;
  background-color: red;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.navbar-brand {
  color: red;
}


